.hero-menu-container {
    line-height: 1.4;

    // Sub-contenedores
    .hero-menu-sub-container {
        list-style: none;
        display: flex;
    }

    // Contenedor de las anclas
    .hero-menu-content-anchor {
        position: relative;

        // Sub-contenedor anidadas en las anclas
        .hero-menu-dropdown {
            display: block;
            position: absolute;
            top: 100%;
            width: 7rem;
        }
    }

    ul {
        list-style: none;
    }
}